.faq{display:flex;flex-direction:column;width:100%;margin:0 auto;background-color:#fff}.faq__box{padding:0 20px}.faq__wrapper{width:65%;margin-right:auto;margin-left:5%}.faq__title,.faq__contact__title{position:relative;font-size:35px;line-height:48px;font-weight:800;margin:30px 0;text-transform:uppercase}.faq__accordion{position:relative;display:flex;justify-content:space-between;border:none;border-bottom:1px solid #000;vertical-align:middle;background-color:rgba(0,0,0,0);width:100%;margin:auto;padding:18px;font-size:20px;line-height:27.24px;text-align:left;transition:.4s;cursor:pointer}.faq__accordion:after{content:"â";font-size:13px;float:right;margin-left:5px}.faq__panel{font-size:20px;line-height:27.24px;max-height:0;overflow:hidden;transition:max-height .2s ease-out;white-space:pre-wrap}.faq__panel p{position:relative;padding:20px}.faq .active:after{content:"â"}.faq__legal{color:#000;text-decoration:none;width:max-content;text-align:center;background-color:#fff;border:1px solid #000;border-radius:5px;padding:8px 15px;margin:30px 0;font-weight:800;cursor:pointer}.faq__legal:hover{background-color:#000;color:#fff}.faq__contact{display:flex;flex-direction:column;justify-content:flex-start;margin:30px 0}.faq__contact p:nth-child(1n+2){padding-bottom:25px}.faq__contact a{text-decoration:none;color:#000}@media screen and (max-width: 768px){.faq__wrapper{width:80%;margin:auto}.faq__box{padding:0;width:100vw;margin-left:-13%}.faq__accordion{padding:18px 10%}}